	import uniCard from "@/components/uni-card/uni-card"
	export default {
		components:{uniCard},
		data(){
			 return{
				tip:[],
				title:'',
				contents:'', 
				note:'',
				timer:'', // 定时器
				currentTime: new Date()
			 }
		},
		created() {
		    var _this = this; 
		    this.timer = setInterval(function() {
		      _this.currentTime = 
		        new Date().getFullYear() +
		        "-" +
		        (new Date().getMonth() + 1) +
		        "-" +
		        new Date().getDate() +
		        " " +
		        new Date().getHours() +
		        ":" +
		        new Date().getMinutes() +
		        ":" +
		        new Date().getSeconds();
		    }, 1000);
		  },
		beforeDestroy() {
		    if (this.timer) {
		      clearInterval(this.timer); 
		    }
		  },
		 	appendZero(obj) {
		 	if (obj < 10) {
		 	return "0" + obj;
		 	} else {
		 	return obj;
		 	}
		 	},
			getStorage: function(){
				uni.setStorage({
				key:'index',
				data:{},
				success:function(){
					console.log('存入成功');
				},
				fail:function(){
					console.log('存入失败');
				}
		})
		},
		 methods:{
			add(index){
				if(this.contents.length == 0){
					uni.showToast({
						title:'内容为空',
						mask:true,
						image:'/static/cuowu.png',
					})
				}else{
				this.tip.unshift({contents:this.contents,title:this.title,note:this.note,icon:this.icon,currentTime:this.currentTime});
				// 置空输入框
				this.contents='';
				this.note='';
				this.title='';
				}
			},
			del(index){
				this.tip.splice(index,1)
			},
		}
}
